import { Injectable } from '@angular/core';
import { Subject }    from 'rxjs/Subject';
 
@Injectable()
export class ParentVisibleService {
 
  private parentVisible = new Subject<boolean>();
 
  parentVisibleStatus$ = this.parentVisible.asObservable();
 
  setVisibility(parentVisible: boolean) {
    this.parentVisible.next(parentVisible);
  }
}


